
@charset "UTF-8";
.map-china {
  z-index: 0;
  /*隐藏logo*/
}
.map-china .tdt-infowindow-content-wrapper {
    background-color: rgba(0, 0, 0, 0.4);
}
.map-china .tdt-infowindow-content-wrapper .tdt-infowindow-content {
      margin: 16px;
}
.map-china .tdt-infowindow-content-wrapper table {
      color: #fff;
      font-weight: lighter;
}
.map-china .tdt-infowindow {
    cursor: auto;
}
.map-china .tdt-infowindow-tip-container {
    height: 13px;
}
.map-china .tdt-infowindow-tip-container .tdt-infowindow-tip {
      background-color: rgba(0, 0, 0, 0.4);
      box-shadow: none;
}
.map-china .tdt-control-copyright {
    display: none !important;
}



