
@charset "UTF-8";
.chart_main {
  width: 659px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-content: center;
}
.chart_bg {
  position: relative;
  margin-top: -0.2rem;
}
.chart_bg .message {
    width: 110%;
    color: #fff;
    margin: 0 auto;
    padding: .2rem;
    background: #0b0d1d;
    transition: all .2s ease-in;
    box-sizing: border-box;
    position: absolute;
    left: -5%;
    right: 0;
    top: 340px;
    z-index: -1;
    opacity: 0;
}
.chart_bg .message p {
      font-size: .18rem;
      line-height: .24rem;
      width: 100%;
      height: 100%;
      text-overflow: ellipsis;
      overflow: hidden;
}
.chart_bg .show {
    z-index: 99;
    opacity: 1;
    transition: all .2s ease-in;
}
.chart_bg .point {
    width: 231px;
    height: 213px;
    cursor: pointer;
    position: absolute;
    top: 96px;
    left: 223px;
    z-index: 99;
}
.chart_txt {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 38px;
  left: 0;
  text-align: center;
  color: #fff;
  z-index: 10;
}
.unitBox {
  position: relative;
  top: 30px;
  left: 0px;
}
.unitBox > span {
  font-size: 14px;
  position: absolute;
  top: 37px;
  left: 44px;
}
.chart_txt > p {
  font-size: 16px;
  margin-top: 75px !important;
}
.middleChart .chart_txt p:nth-child(2) {
  font-size: 16px;
  margin-top: 110px !important;
}
.middleChart .chart_txt p:nth-child(3) {
  font-size: 16px;
  margin-top: 10px !important;
}
.posi-sty {
  position: relative;
  top: -72px;
  /*	z-index: 999;*/
}
#speedometer_f {
  position: absolute;
  top: 145px;
  left: 103px;
  z-index: 9;
}
#speedometer_m {
  position: absolute;
  top: 96px;
  left: 223px;
  z-index: 8;
}
#speedometer_b {
  position: absolute;
  top: 145px;
  left: 427px;
  z-index: 9;
}

/*d3样式*/
.unselectable {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* css formats for the gauge */
.gauge .domain {
  stroke-width: 2px;
  stroke: #35c5ed;
}
.gauge .tick line {
  stroke: #FFFFFF;
  stroke-width: 2px;
}
.gauge line {
  stroke: #FFFFFF;
}
.gauge .arc,
.gauge .cursor {
  opacity: 0;
}
.gauge .major {
  fill: #FFFFFF;
  font-size: 12px;
  font-weight: normal;
}
.unselectable {
  font-size: 14px;
}



