
.product-disaster-wrapper {
  height: calc(100% - 0.5rem);
}
.product-disaster-wrapper .true-title {
    width: calc(100% - 3.4rem);
    height: 0.9rem;
    position: absolute;
    top: 0;
    left: 1.7rem;
}
.product-disaster-wrapper .true-title p {
      width: 100%;
      height: 100%;
      line-height: 0.9rem;
      font-size: 0.36rem;
      font-weight: bold;
      text-align: center;
}
.product-disaster-wrapper .bigW-intro {
    height: 0 !important;
}
.product-disaster-wrapper .bigW-option .selectBtn-wrapper .selectBtn:first-child {
    width: 1.3rem;
}
.product-disaster-wrapper .bigW-option .select-wrapper {
    margin-right: 0.2rem;
    /*.arrow-cont {
        li {
          height: 0.36rem !important;
        }
      }*/
}
.product-disaster-wrapper .bigW-option p {
    margin-right: 0.2rem;
}



