
.procuct-title-info {
  /*margin: 0 auto 0.2rem;*/
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.procuct-title-info p {
    width: 4.8rem;
    /*margin-right: .7rem;*/
    display: flex;
    justify-content: space-between;
}
.btnwrapper {
  margin-bottom: 0.1rem;
  padding-right: 0.3rem;
  height: 0.4rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.btnwrapper > div:nth-child(1), .btnwrapper > div:nth-child(2) {
    margin-right: 0.2rem;
}
.map-echarts-wrapper {
  height: calc(100% - 0.84rem);
  margin: 0 auto;
}
.map-echarts-wrapper .map {
    width: 100%;
    height: 100% !important;
    position: relative;
}
.map-echarts-wrapper .map .map-title {
      width: 50%;
      margin: 0 auto;
      position: absolute;
      top: 0;
      left: 25%;
      z-index: 2;
      padding-top: 0.3rem;
}
.map-echarts-wrapper .map .map-title p {
        font-size: 0.26rem;
        font-weight: normal;
        color: #fff;
        text-align: center;
}
.map-echarts-wrapper .map .toggle-map-wrapper {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}
.map-echarts-wrapper .map .visual-map {
      width: 1rem;
      position: absolute;
      left: 1.6rem;
      bottom: 1rem;
      z-index: 2;
}
.map-echarts-wrapper .map .visual-map .unchecked {
        color: #666;
}
.map-echarts-wrapper .map .visual-map .unchecked:before {
        background-color: #666 !important;
}
.map-echarts-wrapper .map .visual-map li {
        font-size: 14px;
        color: #fff;
        text-align: left;
        height: 0.24rem;
        line-height: 0.24rem;
        position: relative;
        cursor: pointer;
}
.map-echarts-wrapper .map .visual-map li span {
          display: inline-block;
          height: 0.24rem;
          line-height: 0.24rem;
}
.map-echarts-wrapper .map .visual-map li:before {
        width: 0.1rem;
        height: 0.24rem;
        cursor: pointer;
        content: '';
        display: inline-block;
        margin-right: 0.1rem;
        position: absolute;
        top: 0;
        left: -0.2rem;
}
.map-echarts-wrapper .map .visual-map li:nth-child(1):before {
        background-color: #db590e;
}
.map-echarts-wrapper .map .visual-map li:nth-child(2):before {
        background-color: #e48a13;
}
.map-echarts-wrapper .map .visual-map li:nth-child(3):before {
        background-color: #c6a510;
}
.map-echarts-wrapper .map .visual-map li:nth-child(4):before {
        background-color: #c9e110;
}
.map-echarts-wrapper .map .visual-map li:nth-child(5):before {
        background-color: #fdff1b;
}
.map-echarts-wrapper .map .visual-map li:nth-child(6):before {
        background-color: #0071b3;
}
.map-echarts-wrapper .map .timeScroll-wrapper {
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      display: flex;
      justify-content: center;
}
.map-echarts-wrapper .map .rank-echarts-wrapper {
      position: absolute;
      /*top: 1.01rem;*/
      top: 6%;
      right: 0.56rem;
      width: 3.13rem;
}
.map-echarts-wrapper .map .rank-echarts-wrapper .rank-btn {
        display: flex;
        justify-content: center;
        width: 80%;
        margin: 0.1rem auto;
}
.map-echarts-wrapper .map .rank-echarts-wrapper .rank-title {
        height: 0.6rem;
        line-height: 0.6rem;
        color: #dddee0;
        font-size: 0.18rem;
        text-align: center;
}
.map-echarts-wrapper .map .rank-echarts-wrapper .show-button {
        height: 0.4rem;
        line-height: 0.4rem;
        font-size: 0.16rem;
        color: #034058;
        text-align: center;
        cursor: pointer;
}
.map-echarts-wrapper .map .rank-echarts-wrapper .show-button.active i {
          transform: rotate(180deg);
}
.map-echarts-wrapper .map .rank-echarts-wrapper .show-button i {
          display: inline-block;
          transition: all .5s ease;
}
.map-echarts-wrapper .map .rank-echarts-wrapper .show-button:hover {
        color: #0174af;
}
.cont {
  text-align: center;
  color: #00B064;
  /*height: ;*/
}
.cont h2 {
    font-size: 0.34rem;
}
.right-bar-wrapper ul {
  text-align: center;
}
.right-bar-wrapper ul li:nth-child(1) .iconfont, .right-bar-wrapper ul li:nth-child(2) .iconfont, .right-bar-wrapper ul li:nth-child(3) .iconfont, .right-bar-wrapper ul li:nth-child(4) .iconfont, .right-bar-wrapper ul li:nth-child(5) .iconfont {
    font-size: 0.5rem;
}
.right-bar-wrapper ul li:nth-child(1) .iconfont:hover, .right-bar-wrapper ul li:nth-child(2) .iconfont:hover, .right-bar-wrapper ul li:nth-child(3) .iconfont:hover, .right-bar-wrapper ul li:nth-child(4) .iconfont:hover, .right-bar-wrapper ul li:nth-child(5) .iconfont:hover {
      color: #00B064;
}
echarts {
  width: 10rem;
}



